@import url(https://fonts.googleapis.com/css2?family=Arvo:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Syne&display=swap);
:root {
	--foreground-color: teal;
	--light-blue: #03a9f4;
	--light-blue-50: #e1f5fe;
	--light-blue-100: #b3e5fc;
	--light-blue-200: #81d4fa;
	--light-blue-300: #4fc3f7;
	--light-blue-400: #29b6f6;
	--light-blue-500: #03a9f4;
	--light-blue-600: #039be5;
	--light-blue-700: #0288d1;
	--light-blue-800: #0277bd;
	--light-blue-900: #01579b;
}

body {
	margin: 0;
	box-sizing: border-box;
	font-family: 'Arvo', serif;
	color: teal;
	color: var(--foreground-color);
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

text {
	font-family: 'Syne', sans-serif;
	margin: 0;
	padding: 0;
}

::selection {
	background-color: teal;
	background-color: var(--foreground-color);
	color: white;
}

::-webkit-input-placeholder {
	color: teal;
}

:-ms-input-placeholder {
	color: teal;
}

::-ms-input-placeholder {
	color: teal;
}

::placeholder {
	color: teal;
}

/* scroll bar */

::-webkit-scrollbar {
	width: 10px;
	z-index: 100;
}

::-webkit-scrollbar-track {
	background-color: #e1f5fe;
	background-color: var(--light-blue-50);
	border-radius: 10px;
}

::-webkit-scrollbar-thumb {
	background-color: rgb(0, 216, 216);
	border-radius: 10px;
}
::-webkit-scrollbar-thumb:active {
	background-color: rgb(0, 179, 179);
}

.app {
	margin: 0 10vw;
	text-align: center;
}
.app * {
	outline: none;
}
@media screen and (max-width: 992px) {
	.app {
		margin: 0 6vw;
	}
}
@media screen and (max-width: 568px) {
	.app {
		margin: 0 4vw;
	}
}

header h1 {
	text-transform: uppercase;
}
header p {
	font-family: 'Syne', sans-serif;
}

.row {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}
.inputs {
	flex: 5 1;
	min-width: 400px;
	/* background-color: rgb(235, 216, 192); */
}
.inputs * {
	color: teal;
}
.logs {
	width: 200px;
	min-width: 200px;
	flex: 1 1 auto;
	/* background-color: burlywood; */
}
.intro {
	display: flex;
}
.intro > * {
	border-radius: 5px;
	border: 1px solid teal;
	background-color: white;
}
.intro > *:focus {
	border-radius: 5px;
	border: 1px solid var(--light-blue-500);
	background-color: var(--light-blue-50);
}
.url {
	flex: 1 1 auto;
	height: auto;
	margin: 0 5px;
	padding: 10px;
	resize: none;
}

.methods {
	width: 90px;
	display: inline-block;
	cursor: pointer;
	padding-left: 10px;
}
.send {
	cursor: pointer;
	padding: 10px 15px;
}
.send:disabled {
	cursor: not-allowed;
}
.send:hover {
	border-color: var(--light-blue-500);
}

.input_element {
	margin: 10px 0 0 0;
	display: flex;
	flex-direction: column;
}
.input_element label {
	text-align: start;
	padding: 15px 0 15px 15px;
	background-color: var(--light-blue-50);
	border: 1px solid teal;
	border-bottom: none;
	border-radius: 5px 5px 0 0;
}
.input_element textarea {
	resize: none;
	padding: 15px;
	border: 1px solid teal;
	border-radius: 0 0 5px 5px;
}
.input_element textarea:focus {
	background-color: var(--light-blue-50);
}
footer {
	margin: 20px 0;
}

